
.slideBar{
    width: 290px;
    font-size: 14px;
    float: right;
    margin-bottom: 20px;
    min-height: 500px;
}
.panel{
   padding: 10px;
  border-radius: 0 0 3px 3px;
  background-color: #fff;
  line-height: 2em;
  margin-bottom: 13px;
}
.panel  a {
    color: #778087;
    text-decoration: none;
}
.panel  a:hover{
  text-decoration: underline;
}
.sep10 {
    height: 10px;
}
.ads img{
  width: 100%;
}
.friendship-community{
    margin: 4px 0;
    list-style: none;
    padding:0 10px;
}
.friendship-community img{
  width: 150px;
  height: auto;
}
.friendship{
  padding: 0;
}
.panel_header{
    color: #51585c;
    border-radius: 3px 3px 0 0;
    padding: 10px;
    background-color: #f6f6f6;
}
.span-info{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}
.header{
    border-radius: 3px 3px 0 0;
    padding: 10px;
    background-color: #f6f6f6;
    color: #51585c;
    border-radius: 3px 3px 0 0;
}
.userInfo{
    background-color: #fff;
    border-radius:0 0 3px 3px ;
    padding: 10px;
    border-top: 1px solid #e5e5e5;
    overflow: hidden;
}
.bread_head a{
    color: #80bd01;
    text-decoration: none;
}
.user_big_avatar{
    float: left;
    margin-right: 10px;
}
.user_big_avatar::after{
    clear: both
}
.user_big_avatar img{
    width: 40px;
    height: 40px;
    border-radius: 3px;
    float: left;
    vertical-align: middle;
}
.user_big_avatar span{
    color: #778087;
    margin-left: 10px;
    line-height: 2em;
}
.user_profile{
    clear: both;
    margin-top: 50px;
}
.score{
    color:#333;
}
.penal .otherInfo{
    color: #778087;
    text-decoration: none;
}



