
.container{
  min-height:800px;
}
.container_inner {
  width: 90%;
  min-width: 960px;
  max-width: 1400px;
  margin: 20px auto;
}
.author_container{
    padding: 0;
    margin-right: 305px;
}
.panel{
    margin-bottom: 13px;
    border-radius: 3px 3px 3px 3px;
}
.bread_head{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    color: #999;
}
.userInfo{
    background-color: #fff;
    border-radius:0 0 3px 3px ;
    padding: 10px;
    border-top: 1px solid #e5e5e5;
    overflow: hidden;
}
.bread_head a{
    color: #80bd01;
    text-decoration: none;
}
.user_big_avatar{
    float: left;
    margin-right: 10px;
}
.user_big_avatar::after{
    clear: both
}
.user_big_avatar img{
    width: 40px;
    height: 40px;
    border-radius: 3px;
    float: left;
    vertical-align: middle;
}
.user_big_avatar span{
    color: #778087;
    margin-left: 10px;
    line-height: 2em;
}
.user_profile{
    clear: both;
    margin-top: 50px;
}
.score{
    color:#333;
}
.penal .otherInfo{
    color: #778087;
    text-decoration: none;
}



