
.lellansin{
      width: 30px;
    height: 30px;
    border-radius: 3px;
    vertical-align: middle;
}
.listInner{
    position: relative;
    padding: 10px 0 10px 10px;
    font-size: 14px;
    line-height: 2em;
}
.countOfReplies{
    color: #9e78c0;
    text-align: center;
}
.countOfvisits{
    font-size: 10px;
    color: #b4b4b4;
}
.reply_count{
  width: 70px;
  text-align: center;
  display: inline-block
}
.replay_title{
      color: #333;
      text-decoration: none;
}
.replay_title:hover{
  text-decoration: underline;
}
.replay_top{
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
}
.replay_type{
    background-color: #e5e5e5;
    color: #999;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    font-size: 12px;
}
.lastTime{
  float: right;
  margin-right: 10px;
  color: #778087!important;
  min-width: 50px;
  display: inline-block;
  text-align: right;
  text-decoration: none;
  font-size: 12px;
}



