
.footMain{
position: relative;
clear: both;
background: #fff;
}
.footer_inner{
    overflow: hidden;
    padding: 20px 0;
    width: 90%;
    max-width: 1400px;
    min-width: 960px;
    margin: 0 auto;
    background-color: #fff;
    color: #ababab;
    font-size: 14px;
}
.footer_inner p{
    margin: 20px 0;
}
.footer_inner a{
    color: #08c;
}



