
@import './../css/markdown.css';
.container{
  min-height:800px;
}
.container_inner {
  width: 90%;
  min-width: 960px;
  max-width: 1400px;
  margin: 20px auto;
}
.detailContainer{
    padding: 0;
    margin-right: 305px;
}
.panel{
    margin-bottom: 13px;
    border-radius: 3px 3px 3px 3px;
}
.topic_header{
    background-color: #fff;
    border-radius: 3px 3px 0 0;
    line-height: 1em;
    padding: 0px 0px 10px 0;
    border-bottom: 1px solid #e5e5e5;
}
.topic_full_title{
    font-size: 22px;
    font-weight: 700;
    margin: 8px 0;
    display: inline-block;
    vertical-align: bottom;
    width: 75%;
    line-height: 130%;
    font-family: "Helvetica Neue","Luxi Sans","DejaVu Sans",Tahoma,"Hiragino Sans GB",STHeiti,sans-serif!important;
}
.put_top {
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
}
.topic_tab>span::before{
    content:"• "
}
.topic_tab>span{
    font-size: 12px;
    color: #838383;
}
.innerTopic{
    font-size: 14px;
    word-break: break-word;
    color: #333;
    line-height: 2em;
    background-color: #fff;
    padding: 10px;
    border-radius: 0 0 3px 3px;
}
.topic_content{
    margin: 0 10px;
}



